.nav-links {
  &__list {
    padding: 0;
    margin: 0;
    list-style: none;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    @media (max-width: 768px) {
      display: none;
    }

    &--bottom {
      border-bottom: 1px solid #f6f5f4;
    }

    & a {
      padding: 18px 16px;
      font-family: $mainFontMedium;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      display: flex;
      align-items: center;
      box-sizing: border-box;

      text-decoration: none;

      color: $primary-color;

      &:focus,
      &:hover {
        opacity: 0.5;
      }
    }
  }
}
